<template>
  <div class="pageContent">
    <el-form ref="ruleForm" :model="ruleForm" label-width="150px">
      <h3 class="header">企业信息认证</h3>
      <el-card class="box-card">
        <div class="pageHead">基础信息</div>
        <el-form-item label="公司名称：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.companyName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="显示名称：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.displayName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="注册地址：" required>
          <div class="flex-row-start">
            <el-form-item prop="addressProvinceCode" style="margin-right:10px;">
              <el-select @change="address1" v-model="ruleForm.addressProvinceCode" placeholder="请选择省">
                <el-option v-for="(item,index) in addressList1" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="addressCityCode" style="margin-right:10px;">
              <el-select @change="address2" v-model="ruleForm.addressCityCode" placeholder="请选择市">
                <el-option v-for="(item,index) in addressList2" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="addressCountyCode">
              <el-select v-model="ruleForm.addressCountyCode" placeholder="请选择区">
                <el-option v-for="(item,index) in addressList3" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input style="width:400px;" v-model="ruleForm.addressDetails" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="统一社会信用代码：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.businessNumber" maxlength="18" show-word-limit></el-input>
        </el-form-item>
      </el-card>
      <el-card class="box-card" style="margin-top:20px;">
        <div class="pageHead">法人信息</div>
        <el-form-item label="法人姓名：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.legalPersonName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="身份证号：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.legalPersonNumber" maxlength="18" show-word-limit></el-input>
        </el-form-item>
      </el-card>
      <el-card class="box-card" style="margin-top:20px;">
        <div class="pageHead">授权人信息</div>
        <el-form-item label="授权人姓名：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.authorizedPersonName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="联系地址：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.authorizedPersonAddress" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="手机号：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.authorizedPersonMobilephone" maxlength="11" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="身份证号：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.authorizedPersonNumber" maxlength="18" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="固定电话：">
          <el-input style="width:400px;" v-model.trim="ruleForm.telephone" maxlength="15" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="官方网站：">
          <el-input style="width:400px;" v-model.trim="ruleForm.website" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-card>
      <el-card class="box-card" style="margin-top:20px;">
        <div class="pageHead">结算信息</div>
        <el-form-item label="开户城市：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.openBankCity" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="开户银行：" required>
          <el-select style="width:400px;" v-model="ruleForm.openBank">
            <el-option v-for="(item,index) in bankMenu" :key="index" :label="item.key" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开户支行：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.openBranch" maxlength="40" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="开户名称：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.openName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="银行账户：" required>
          <el-input style="width:400px;" v-model.trim="ruleForm.bankAccount" maxlength="20" show-word-limit></el-input>
        </el-form-item>
      </el-card>
      <el-card class="box-card" style="margin-top:20px;">
        <div class="pageHead">资质上传</div>
        <el-form-item label="上传资质：" required>
          <el-upload 
            style="width:440px;" 
            class="upload-demo" 
            :file-list="fileList"
            :action="action" 
            :on-success="handleAvatarSuccess" 
            :on-remove="handleRemove" 
            :before-remove="beforeRemove" 
            :limit="1" 
            :on-exceed="handleExceed"
            >
            <el-button size="small" type="primary">点击上传</el-button>
            <div style="color:#EA2821;" class="el-upload__tip" slot="tip">请上传所有图片的“压缩包”</div>
            <div style="color:#EA2821;" class="el-upload__tip" slot="tip">此处需上传资质照片包括：商家手持身份证正面照、商家手持身份证背面照、营业执照电子版以及法人授权委托书（法人授权委托书、请与本公司运营商议是否上传）</div>
          </el-upload>
        </el-form-item>
        <el-form-item  label="示例照片：">
          <div>
            <span></span>
            <el-image style="margin-right:20px;" :src="require('../../../assets/images/shop/idCardPositive.jpg')"></el-image>
            <el-image :src="require('../../../assets/images/shop/idCardBack.jpg')"></el-image>
          </div>
        </el-form-item>
        <el-form-item prop="type" label="资质声明：">
          <el-checkbox-group v-model="checked">
            <el-checkbox label="我已阅读并同意: 保证本店铺内所售产品具备销售及相关资质，特此声明。" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-card>
      <el-form-item style="margin-top:20px;text-align:center;">
        <el-button :loading="subLoading" type="primary" @click="submitForm('ruleForm')">提 交</el-button>
        <el-button @click="$router.go(-1)">返 回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { insertShopAuthInfo , getShopAuthInfoByShopId ,updateShopAuthInfo } from '@/api/shop/shopAttestation'
import { getAddressLevel } from '@/api/shop/createShop'
export default {
  data(){
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'));
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
        console.log(reg.test(value));
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error('请输入正确的手机号'));
        }
      }
    };
    return{
      ruleForm:{},
      checked:false,
      fileList:[],
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
      rules: {
        authorizedPersonName: [
          { required: true, message: '请输入授权人姓名', trigger: 'blur' },
        ],
        authorizedPersonAddress: [
          { required: true, message: '请输入授权人联系地址', trigger: 'blur' },
        ],
        authorizedPersonMobilephone: [
          { required: true, message: '请输入授权人姓名', trigger: 'blur' },
        ],
        authorizedPersonMobilephone: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        authorizedPersonNumber: [
          { required: true, message: '请填写身份证号码', trigger: 'blur' },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: '证件号码格式有误！',
            trigger: 'blur'
          }
        ],
        openBankCity: [
          { required: true, message: '请输入开户城市，如：北京', trigger: 'blur' },
        ],
        openBranch: [
          { required: true, message: '请输入开户行名称', trigger: 'blur' },
        ],
        openName: [
          { required: true, message: '请输入开户名称', trigger: 'blur' },
        ],
        bankAccount: [
          { required: true, message: '请输入银行账户', trigger: 'blur' },
        ],
        openBank: [
          { required: true, message: '请选择开户银行', trigger: 'change' }
        ],
        businessLicenseCoding: [
          { required: true, message: '请输入营业执照编码', trigger: 'blur' },
        ],
        companyName: [
          { required: true, message: '请输入公司名称', trigger: 'blur' },
        ],
        displayName: [
          { required: true, message: '请输入显示名称', trigger: 'blur' },
        ],
        addressDetails: [
          { required: true, message: '请输入显示名称', trigger: 'blur' },
        ],
        businessNumber: [
          { required: true, message: '请输入统一社会信用代码', trigger: 'blur' },
        ],
        addressProvinceCode: [
          { required: true, message: '请选择省', trigger: 'change' }
        ],
        addressCityCode: [
          { required: true, message: '请选择市', trigger: 'change' }
        ],
        addressCountyCode: [
          { required: true, message: '请选择市', trigger: 'change' }
        ],
        legalPersonName: [
          { required: true, message: '请输入法人姓名', trigger: 'blur' },
        ],
        legalPersonNumber: [
          { required: true, message: '请填写身份证号码', trigger: 'blur' },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: '证件号码格式有误！',
            trigger: 'blur'
          }
        ],
        type: [
          { type: 'array', required: true, message: '请勾选资质声明', trigger: 'change' }
        ],
      },
      bankMenu :[
        {"value":"1002","key":"工商银行"},
        {"value":"1005","key":"农业银行"},
        {"value":"1026","key":"中国银行"},
        {"value":"1003","key":"建设银行"},
        {"value":"1001","key":"招商银行"},
        {"value":"1066","key":"邮政储蓄"},
        {"value":"1020","key":"交通银行"},
        {"value":"1004","key":"浦发银行"},
        {"value":"1006","key":"民生银行"},
        {"value":"1009","key":"兴业银行"},
        {"value":"1010","key":"平安银行"},
        {"value":"1021","key":"中信银行"},
        {"value":"1025","key":"华夏银行"},
        {"value":"1027","key":"广发银行"},
        {"value":"1022","key":"光大银行"},
        {"value":"1032","key":"北京银行"},
        {"value":"1056","key":"宁波银行"}
      ],
      action:'',              // 上传地址
      pageEdit:false,
      subLoading:false
    }
  },
  created(){
    this.action = `/sys/business/shopInfo/uploadAuthFile?shopId=${this.shop.shopId}`
    this.pageEdit = this.$route.query.edit ? true : false;
    this.getAddressInput(1,'')
    if(this.pageEdit) this.getData()
  },
  methods:{
    // 编辑时的查看数据
    getData(){
      getShopAuthInfoByShopId(this.shop.shopId).then(res=>{
        console.log('店铺资质数据',res)
        this.authType = res.data.authType
        this.fileList.push(res.data.urlObject)
        // console.log('this.fileList',this.fileList)
        this.getAddressInput(2,res.data.addressProvinceCode)
        this.getAddressInput(3,res.data.addressCityCode)
        this.ruleForm = res.data
      })
    },
    // 提交
    submitForm(formName){
      if(!this.ruleForm.companyName){
        this.$message.error('请输入公司名称');
        return;
      }
      if(!this.ruleForm.displayName){
        this.$message.error('请输入显示名称');
        return;
      }
      if(!this.ruleForm.addressProvinceCode){
        this.$message.error('请选择省');
        return;
      }
      if(!this.ruleForm.addressCityCode){
        this.$message.error('请选择市');
        return;
      }
      if(!this.ruleForm.addressCountyCode){
        this.$message.error('请选择区');
        return;
      }
      if(!this.ruleForm.addressDetails){
        this.$message.error('请输入详细地址');
        return;
      }
      if(!this.ruleForm.legalPersonName){
        this.$message.error('请输入法人信息');
        return;
      }

      if(!this.ruleForm.legalPersonNumber){
        this.$message.error('请输入身份证号');
        return;
      }
      let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/
      if(!reg.test(this.ruleForm.legalPersonNumber)){
        this.$message.error('输入的身份证号有误');
        return;
      }
      if(!this.ruleForm.authorizedPersonName){
        this.$message.error('请输入授权人信息');
        return;
      }
      if(!this.ruleForm.authorizedPersonAddress){
        this.$message.error('请输入授权人联系地址');
        return;
      }
      if(!this.ruleForm.authorizedPersonMobilephone){
        this.$message.error('请输入授权人手机号');
        return;
      }
      if(!this.ruleForm.authorizedPersonNumber){
        this.$message.error('请输入授权人身份证号');
        return;
      }
      if(!reg.test(this.ruleForm.authorizedPersonNumber)){
        this.$message.error('输入的身份证号有误');
        return;
      }

      if(!this.ruleForm.openBankCity){
        this.$message.error('请输入开户城市');
        return;
      }
      // 选择开户支行
      if(!this.ruleForm.openBank){
        this.$message.error('请选择开户银行');
        return;
      }
      if(!this.ruleForm.openBranch){
        this.$message.error('请输入开户支行');
        return;
      }
      if(!this.ruleForm.openName){
        this.$message.error('请输入开户名称');
        return;
      }
      if(!this.ruleForm.bankAccount){
        this.$message.error('请输入正确的银行账户');
        return;
      }
      if(!this.ruleForm.authFileUrl){
        this.$message.error('请上传资质证明的压缩包');
        return;
      }
      this.subLoading = true;
      this.ruleForm.authType = 2
      this.ruleForm.shopId = this.shop.shopId
      this.ruleForm.bankAccount = this.ruleForm.bankAccount.replace(/\s+/g,"")
      console.log('提交的请求数据',this.ruleForm)
      if(!this.pageEdit) this.newSub()
      if(this.pageEdit) this.editSub()
    },
    // 新用户提交
    newSub(){
      insertShopAuthInfo(this.ruleForm).then(res=>{
        console.log('店铺认证',res)
        if(res.code == 200){
          this.$message({
            message: '提交成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.push({path:'/shop/shopAttestation'})
            this.subLoading = false;
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 老用户编辑
    editSub(){
      updateShopAuthInfo(this.ruleForm).then(res=>{
        console.log('店铺认证',res)
        if(res.code == 200){
          this.$message({
            message: '提交成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.push({path:'/shop/shopAttestation'})
            this.subLoading = false;
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      console.log('执行了')
      this.addressList2 = [];
      if(this.ruleForm.addressCityCode) this.ruleForm.addressCityCode = '';
      this.addressList3 = [];
      if(this.ruleForm.addressCountyCode) this.ruleForm.addressCountyCode = '';
      this.getAddressInput(2,val);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      this.addressList3 = [];
      if(this.ruleForm.addressCountyCode) this.ruleForm.addressCountyCode = '';
      this.getAddressInput(3,val);
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log('获取下拉框地址',res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
    handleAvatarSuccess(res, file) {
      console.log(res)
      console.log(file)
      this.ruleForm.authFileUrl = res.data.name
      this.ruleForm.authFileName = res.data.orginName
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制上传 1 个文件`);
    },
    handleRemove(file, fileList) {
      console.log('删除',file, fileList);
      this.ruleForm.authFileUrl = ''
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.header{
  width: 100%;
  line-height: 40px;
  text-align: center;
  margin-bottom: 40px;
}
</style>